import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FinanceManager.module.css";
import RndProjectHeadDetailCard from "./RndProjectHeadDetailCard";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useCallback, useEffect, useState } from "react";
import AddRndProjectHeadDetail from "./AddRndProjectHeadDetail";
import { useRndOneHeadSectionList } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import { useLocation } from "react-router-dom";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
const RndProjectHeadDetail = ({ qcid, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [add, setAdd] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    rndOneHeadSectionList,
    rndOneHeadSectionListLoading,
    rndOneHeadSectionListRefetch,
  } = useRndOneHeadSectionList({
    data: {
      hid: getQuery.state?.headId,
      page: page,
      limit: 10,
      search: search,
      flow: "PROJECTS",
    },
    skip: !getQuery.state?.headId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (rndOneHeadSectionList?.list?.length) {
      let obj = nextPage(rndOneHeadSectionList?.list?.length);
      setState(() => obj);
    }
  }, [rndOneHeadSectionList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    rndOneHeadSectionListRefetch();
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{getQuery.state?.headName ?? ""}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {rndOneHeadSectionList?.list?.map((rnd, index) =>
            rndOneHeadSectionList?.list?.length === index + 1 ? (
              <div key={rnd?._id} ref={ref}>
                <RndProjectHeadDetailCard rnd={rnd} />
              </div>
            ) : (
              <RndProjectHeadDetailCard rnd={rnd} key={rnd?._id} />
            )
          )}

          {rndOneHeadSectionListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            rndOneHeadSectionList?.list?.length === 0 && (
              <EmptyMessage
                title="No items found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            rndOneHeadSectionList?.list?.length === 0 && (
              <EmptyMessage
                title="No items."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {add && (
        <AddRndProjectHeadDetail
          onClose={onToggle}
          hid={getQuery.state?.headId}
          onRefetch={onRefetch}
          instituteId={instituteId}
        />
      )}
    </>
  );
};
export default RndProjectHeadDetail;
