import { useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useRndAddActivities } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import FormFile from "../../../Ui/Input/FormFile";
import FormTextarea from "../../../Ui/Input/FormTextarea";

const AddRndActivitiesHeadDetail = ({ onClose, onRefetch, hid }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    srn: "",
    title: "",
    description: "",
    linkUrl: "",
    attach: "",
  });

  const [rndAddActivities] = useRndAddActivities();
  const [fileUpload] = useFileUpload();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            attach: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && hid) {
      setDisabled((pre) => !pre);
      rndAddActivities({
        hid: hid,
        bodyParameter: {
          ...state,
          link: state.linkUrl,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_activities")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("sr_no_only")}
              placeholder={t("sr_no_placeholder")}
              name={"srn"}
              value={state.srn}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["srn"]}
            />
            <FormInput
              label={t("title")}
              placeholder={t("title_placeholder")}
              name={"title"}
              value={state.title}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["title"]}
            />
            <FormTextarea
              label={t("description")}
              placeholder={t("description_placeholder")}
              name="description"
              type="text"
              value={state.description}
              onChange={onChange}
              errorShow={errorField["description"]}
            />

            <FormInput
              label={t("link")}
              placeholder={t("link_placeholder")}
              name={"linkUrl"}
              value={state.linkUrl}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["linkUrl"]}
            />
            <FormFile
              label={t("add_attachment")}
              name={"add_attachment"}
              value={
                state.attach
                  ? {
                      key: state.attach,
                      name: "View",
                    }
                  : ""
              }
              onChange={onFileUpload}
              errorShow={errorField["attach"]}
            />
            <CreateButton label="add" onAction={onAdd} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default AddRndActivitiesHeadDetail;
