import { useState } from "react";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormInput from "../../../Ui/Input/FormInput";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useRndAddResearchPaper } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Income/Income.module.css";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import FormFile from "../../../Ui/Input/FormFile";
import InstituteStaff from "../../InstituteStaff/InstituteStaff";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import styleEdit from "../../../../Profile/Institute/Pages/Site/SiteEdit.module.css";
import InstituteDepartment from "../../InstituteDepartment/InstituteDepartment";

const AddRndResearchPaperHeadDetail = ({
  onClose,
  onRefetch,
  hid,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    collaboration: "",
    title: "",
    staffData: "",
    departmentData: "",
    category: "",
    funding_agency: "",
    linkUrl: "",
    attach: "",
    add_as: "",
  });

  const [rndAddResearchPaper] = useRndAddResearchPaper();
  const [fileUpload] = useFileUpload();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setState((prev) => ({
            ...prev,
            attach: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onAdd = () => {
    let validation = requiredField(state, ["add_as"]);
    if (existingRequiredField(validation) && hid) {
      setDisabled((pre) => !pre);
      rndAddResearchPaper({
        hid: hid,
        bodyParameter: {
          ...state,
          link: state.linkUrl,
          staff: state.staffData?._id,
          department: state.departmentData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  const onSelectDepartment = (assing) => {
    setState((prev) => ({
      ...prev,
      departmentData: assing,
      add_as: "",
    }));
  };
  const onSelectStaff = (assing) => {
    setState((prev) => ({
      ...prev,
      staffData: assing,
      add_as: "",
    }));
  };

  console.info("dsvg skd", state);
  return (
    <>
      {state.add_as === "DEP" ? (
        <InstituteDepartment
          onSelectStaff={onSelectDepartment}
          title={t("select_department")}
          onClose={() =>
            setState((prev) => ({
              ...prev,
              add_as: "",
            }))
          }
          selectedId={state.departmentData?._id}
          instituteId={instituteId}
        />
      ) : state.add_as === "STA" ? (
        <InstituteStaff
          onSelectStaff={onSelectStaff}
          title={t("select_staff")}
          onClose={() =>
            setState((prev) => ({
              ...prev,
              add_as: "",
            }))
          }
          selectedId={state.staffData?._id}
          instituteId={instituteId}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_research_paper")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("collaboration")}
                placeholder={t("collaboration_placeholder")}
                name={"collaboration"}
                value={state.collaboration}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["collaboration"]}
              />
              <FormInput
                label={t("title")}
                placeholder={t("title_placeholder")}
                name={"title"}
                value={state.title}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["title"]}
              />

              <div
                className={styleEdit.leading_person_container}
                onClick={() =>
                  setState((prev) => ({
                    ...prev,
                    add_as: "STA",
                  }))
                }
                style={{
                  marginTop: "1rem",
                  width: "100%",
                }}
              >
                <img
                  src={
                    state?.staffData?.staffProfilePhoto
                      ? `${imageShowUrl}/${state?.staffData.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  alt="avatar"
                />
                <div className={styleEdit.leading_person_text}>
                  <h6>
                    {state?.staffData
                      ? `${state?.staffData?.staffFirstName ?? ""} ${
                          state?.staffData?.staffMiddleName ?? ""
                        } ${state?.staffData?.staffLastName ?? ""}`
                      : t("select_staff")}
                  </h6>
                  <p>
                    {t("search_joined_list")}{" "}
                    {errorField["staffData"] && (
                      <span style={{ color: "red", marginLeft: "15px" }}>
                        * {t("form_require_label")}
                      </span>
                    )}
                  </p>
                </div>
              </div>
              <div
                className={styleEdit.leading_person_container}
                onClick={() =>
                  setState((prev) => ({
                    ...prev,
                    add_as: "DEP",
                  }))
                }
                style={{
                  marginTop: "1rem",
                  width: "100%",
                }}
              >
                <img
                  src={
                    state?.departmentData?.staffProfilePhoto
                      ? `${imageShowUrl}/${state?.departmentData.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  alt="avatar"
                />
                <div className={styleEdit.leading_person_text}>
                  <h6>
                    {state?.departmentData?.dName
                      ? `${state?.departmentData?.dName ?? ""}`
                      : t("select_department")}
                  </h6>
                  <p>
                    {t("select_department")}{" "}
                    {errorField["departmentData"] && (
                      <span style={{ color: "red", marginLeft: "15px" }}>
                        * {t("form_require_label")}
                      </span>
                    )}
                  </p>
                </div>
              </div>
              <FormInput
                label={t("category")}
                placeholder={t("category_placeholder")}
                name={"category"}
                value={state.category}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["category"]}
              />

              <FormInput
                label={t("funding_agency")}
                placeholder={t("funding_agency_placeholder")}
                name={"funding_agency"}
                value={state.funding_agency}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["funding_agency"]}
              />

              <FormInput
                label={t("link")}
                placeholder={t("link_placeholder")}
                name={"linkUrl"}
                value={state.linkUrl}
                type={"text"}
                onChange={onChange}
                errorShow={errorField["linkUrl"]}
              />
              <FormFile
                label={t("add_attachment")}
                name={"add_attachment"}
                value={
                  state.attach
                    ? {
                        key: state.attach,
                        name: "View",
                      }
                    : ""
                }
                onChange={onFileUpload}
                errorShow={errorField["attach"]}
              />
              <CreateButton label="add" onAction={onAdd} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddRndResearchPaperHeadDetail;
